<template>
    <div class="bind-bank">
        <div class="input-section">
            <p class="tip top"><span class="iconfont icon-tishi1"></span>请绑定本人的银行卡号</p>
            <div class="cell cardno-cell">
                <span class="cell-name"><span class="necessary">*</span>卡号</span>
                <input @input="checkClearBtn" @textInput="textInutCheck('cardno',$event)" class="cardno-text" type="number" @focus="cardNoTextFocusHandle" @blur="cardNoTextBlurHandle" ref="cardNoText" v-model="cardNoValue"  placeholder="请输入银行卡号" >
                <div v-show="showClear" ref="clearBtn" @click="clearTextHandle" class="text-clear-div"><span class="iconfont icon-close"></span></div>
            </div>
            <div class="cell cardname-cell">
                <span class="cell-name"><span class="necessary">*</span>用户名</span>
                <input class="cardname-text" type="text" ref="cardNameText" v-model="cardNameValue" placeholder="请输入用户名" >
            </div>
            <div class="bank-info">
                <span class="cell-name">银行</span>
                <img class="bank-pic" :src="bankpic" />
            </div>
            <p class="tip bottom"><span class="iconfont icon-tishi1"></span>请输入6位数字密码</p>
            <div class="cell password-cell">
                <span class="cell-name"><span class="necessary">*</span>提现密码</span>
                <input @textInput="textInutCheck('password1',$event)" class="password-text" type="password" ref="passwordText1" v-model="passwordValue1" placeholder="请输入提现密码" >
            </div>
            <div class="cell password-cell">
                <span class="cell-name"><span class="necessary">*</span>密码确认</span>
                <input @textInput="textInutCheck('password2',$event)" class="password-text" type="password" ref="passwordText2" v-model="passwordValue2" placeholder="请再次输入密码" >
            </div>
        </div>
        <mt-button size="large" class="commit-btn" type="default" @click.native="commitHandle">提交</mt-button>
    </div>
</template>
<script>
import { mapState } from "vuex";
import md5 from 'js-md5';
import Vue from "vue";
import { Toast, Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
import "mint-ui/lib/toast/style.css";
import utils from "../../common/js/utils.js";
Vue.component(Button.name, Button);
Vue.component(Toast.name, Toast);

export default {
  name: "bind-bank",
  data() {
    return {
      cardNoValue: "", //卡号
      cardNameValue: "", //用户名
      passwordValue1: "", //提现密码
      passwordValue2: "", //确认密码
      bankpic: "",
      bankcode: "",
      bankname: "",
      showClear: false
    };
  },
  mounted() {
    //this.$route.params
    if (this.$route.params && this.$route.params.cardNo) {
      this.cardNoValue = this.$route.params.cardNo;
      this.cardNameValue = this.$route.params.accountname;
      this.bankcode = this.$route.params.bankcode;
      this.bankpic =
        "https://apimg.alipay.com/combo.png?d=cashier&t=" + this.bankcode;
      this.getBankName();
    }
    this.$store.dispatch("toggleLoading", false);
  },
  computed: {
    ...mapState(["userid", "path"])
  },
  methods: {
    checkClearBtn() {
      this.$nextTick(() => {
        if (!this.cardNoValue) {
          this.showClear = false;
        } else {
          this.showClear = true;
          if (this.cardNoValue.length >= 16 && this.cardNoValue.length <= 19) {
            utils.throttle(this.checkCardInfo, 300, 1000)();
          } else {
            if (this.bankcode) {
              this.bankcode = "";
              this.bankpic = "";
            }
          }
        }
      });
    },
    textInutCheck(type, event) {
      if (!/^[0-9]*$/.test(event.data)) {
        event.returnValue = false;
      }
      if (type == "cardno") {
        if (this.cardNoValue.length >= 19) event.returnValue = false;
      } else if (type == "password1") {
        if (this.passwordValue1.length >= 6) event.returnValue = false;
      } else if (type == "password2") {
        if (this.passwordValue2.length >= 6) event.returnValue = false;
      }
    },
    cardNoTextFocusHandle() {
      if (this.cardNoValue) this.showClear = true;
    },
    cardNoTextBlurHandle() {
      this.showClear = false;
    },
    clearTextHandle() {
      this.cardNoValue = "";
      this.showClear = false;
      this.bankcode = "";
      this.bankpic = "";
    },
    checkCardInfo: function() {
      this.$http
        .post(this.path + "sms/suncode/msgservice.php", {
          type: "bankcard",
          cardno: this.cardNoValue
        })
        .then(response => {
          var respdata = response.body;
          if (respdata.validated == true) {
            this.bankcode = respdata.bank;
            this.bankpic =
              "https://apimg.alipay.com/combo.png?d=cashier&t=" + this.bankcode;
            this.getBankName();
          } else {
            this.bankcode = "";
            this.bankpic = "";
          }
        })
        .catch(response => {});
    },
    getBankName() {
      this.$http
        .post(this.path + "sms/suncode/msgservice.php", {
          type: "bankname",
          bank: this.bankcode
        })
        .then(response => {
          this.bankname = response.body;
        });
    },
    commitHandle() {
      if (!this.bankcode) {
        this.showMsg("请输入正确卡号");
        return;
      }
      if (this.cardNameValue.replace(/\s+/g, "") == "") {
        this.showMsg("请输入账户名");
        return;
      }
      if (this.passwordValue1.length < 6) {
        this.showMsg("请输入6位提现密码");
        return;
      }
      if (this.passwordValue2 != this.passwordValue1) {
        this.showMsg("密码不一致");
        return;
      }
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + "php/suncode/dealfuc.php", {
          fuc: "bank",
          user: this.userid,
          bankname: this.bankname,
          expwd: this.passwordValue1,
          accountname: this.cardNameValue,
          bankno: this.cardNoValue,
          bankcode: this.bankcode
        })
        .then(response => {
          this.$store.dispatch("toggleLoading", false);
          var respdata = response.body;
          if (respdata.stat1.toString() == "200") {
            this.showMsg("绑定成功");
            this.jumpBack();
          } else if (respdata.stat2.toString() == "200"||respdata.stat2.toString()=="203") {
            this.showMsg("修改密码成功");
            this.jumpBack();
          }
        })
        .catch(response => {
          //请求出错处理
          this.$store.dispatch("toggleLoading", false);
          console.log(response);
        });
    },
    jumpBack() {
      let clearId = setTimeout(() => {
        clearTimeout(clearId);
        if (this.$route.name == "bindBank") this.$router.go(-1);
      }, 600);
    },
    showMsg: function(msg) {
      Toast({
        message: msg,
        iconClass: "iconfont icon-tishi1",
        duration: 1000
      });
    }
  }
};
</script>
<style lang="stylus">
.bind-bank {
  padding-top: 0.3rem;

  .input-section {
    padding: 0 0.22rem;
    box-sizing: border-box;

    .tip {
      font-size: 0.14rem;
      color: #8a8a8a;
      letter-spacing: 0.01rem;
      margin-bottom: 0.08rem;

      .icon-tishi1 {
        font-size: 0.19rem;
        margin-right: 0.02rem;
      }

      &.top {
        margin-top: 0.14rem;
      }
    }

    .bank-info {
      margin-top: 0.05rem;
      min-height: 0.5rem;
      display: flex;

      .cell-name {
        margin-left: 0.12rem;
        margin-top: 0.09rem;
        font-size: 0.12rem;
        color: #000;
        letter-spacing: 0.01rem;
      }

      .bank-pic {
        margin-left: 0.13rem;
        height: 40px;
        width: auto;
      }
    }

    .cell {
      margin-top: 0.05rem;
      width: 100%;
      height: 0.34rem;
      border-bottom: 1px #ddd solid;
      display: flex;
      align-items: center;
      position: relative;

      &.password-cell {
        width: 85%;
      }

      .cell-name {
        padding-left: 0.05rem;
        flex-shrink: 0;
        font-size: 0.12rem;
        color: #000;
        letter-spacing: 0.01rem;

        .necessary {
          margin-right: 0.02rem;
        }
      }

      input {
        flex: 1;
        width: 100%;
        -webkit-appearance: none; // 去掉iphone背景阴影
        border: none;
        outline: none;
        box-sizing: border-box;
        -webkit-touch-callout: none;
        color: #333;
        padding-left: 0.22rem;
        font-size: 0.14rem;
        position: relative;
        top: -0.01rem;
        letter-spacing: 0.005rem;

        &::-webkit-input-placeholder {
          color: #ddd;
          font-size: 0.12rem;
          letter-spacing: 0.01rem;
        }
      }

      .text-clear-div {
        position: absolute;
        right: 0px;
        top: 50%;
        transform: translate(-50%, -50%);

        span {
          font-size: 0.16rem;
          color: #8a8a8a;
        }
      }
    }
  }

  .commit-btn {
    background-color: #f8ef94;
    color: #595656;
    margin-top: 0.26rem;
    margin-left: 0.25rem;
    width: calc(100% - 0.5rem);
    font-size: 0.12rem;
    letter-spacing: 0.02rem;
    height: 0.32rem;
    box-shadow: none;
  }
}
</style>


